.newProduct {
  flex: 5;
  h1 {
    margin-top: 15px;
  }
  form {
    margin-top: 10px;
    .addProductItem {
      width: 250px;
      display: flex;
      flex-direction: column;
      margin-bottom: 10px;
    }
    label {
      color: gray;
      font-weight: 600;
      margin-bottom: 10px;
    }
    input {
      padding: 10px;
    }
    select {
      padding: 10px;
    }
    button {
      margin-top: 10px;
      padding: 7px 10px;
      border: none;
      border-radius: 10px;
      background-color: darkblue;
      color: white;
      font-weight: 600;
      cursor: pointer;
    }
  }
}
